*,::after,::before{
  box-sizing: border-box;
}

.container {
  max-width: 960px;
  margin: 15px auto;
  padding: 0 15px;
}

.top {
  padding: 1rem 1rem 5rem;
  text-align: center;
  background-color: #000;
  position: relative;
  margin-bottom: 7rem;

  h1 {
    margin: 0;
    font-size: 2rem;
    color: #fff;
    padding: 1.5rem 0 1rem;
    border-radius: 30px;

    @media (max-width: 600px) {
      font-size: 1rem;
    }
  }
  p {
    color: #ccc;
  }
  img {
    border: 4px solid #fff;
    max-width: 100%;
    border-radius: 50%;
    width: 146px;
    height: 146px;
    position: absolute;
    bottom: -75px;
    margin-left: -75px;
  }
  ul{
    padding-left: 0;

    li{
      display: inline-block;
      margin: 0 0.35rem;

      svg{
        width: 20px;
        fill: #fff;
      }
    }
  }
}

.school {

  h3{
    margin: 3rem 0;
    font-size: 1.5rem;
  }

  &__wapper {
    position: relative;
    padding-left: 200px;

    @media (max-width: 600px) {
      padding-left: 1rem;
    }

    &::before{
      content: '';
      width: 0;
      height: 100%;
      border-right: 2px dashed #000;
      position: absolute;
      left: 170px;

      @media (max-width: 600px) {
        left: 0;
      }

    }
  }
  &__item {
    margin-bottom: 2rem;
    padding-bottom: 1.5rem;
    position: relative;

    &::before{
      position: absolute;
      content: '';
      height: 10px;
      width: 10px;
      background-color: #000;
      border-radius: 50%;
      left: -34px;
      top: 3px;

      @media (max-width: 600px) {
        left: -1.25rem;
      }
    }

    &::after{
      position: absolute;
      content: '';
      height: 0px;
      width: calc(100% + 30px);
      left: -30px;
      bottom: 0;
      border-bottom: 2px dashed #000;

      @media (max-width: 600px) {
        width: calc(100% + 1rem);
        left: -1rem;
      }
    }

    &:last-child::after{
      display: none;
    }

    &__year{
      position: absolute;
      left: -200px;
      top: 2px;

      @media (max-width: 600px) {
        position: relative;
        left: 0;
        top: 0;
        margin-bottom: 1rem;
      }

      br{
        @media (max-width: 600px) {
          display: none;
        }
      }
    }

    &__title{
      margin-bottom: 1rem;
      font-weight: bold;
    }

    &__content{
      line-height: 1.5rem;

      p{
        margin: 0.5rem 0;
      }

      ul{
        margin: 0.5rem 0;
        padding-left: 0;
        list-style: none;

        li{
          margin-bottom: 0.5rem;
          padding-left: 15px;
          position: relative;

          &::before{
            position: absolute;
            content: '';
            height: 3px;
            width: 3px;
            background-color: #000;
            border-radius: 50%;
            left: 0;
            top: 10px;
          }
        }
      }
    }
  }
}
footer{
  text-align: center;
  margin: 2rem 0;
}